"use client";
import React, { useState } from "react";
import {
  Mail,
  Lock,
  Heart,
  Moon,
  Sun,
  Eye,
  EyeOff,
  User,
  Phone,
  Calendar,
  Droplets,
} from "lucide-react";

const DonerRegistration = () => {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    confirmPassword: "",
    firstName: "",
    lastName: "",
    dateOfBirth: "",
    phone: "",
    bloodType: "",
    weight: "",
    lastDonation: "",
    medicalConditions: "",
    address: "",
  });

  const [focusedInput, setFocusedInput] = useState("");
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [emailError, setEmailError] = useState("");
  const [passwordErrors, setPasswordErrors] = useState({
    length: false,
    number: false,
    uppercase: false,
    lowercase: false,
    special: false,
  });
  const [passwordMatch, setPasswordMatch] = useState(true);
  const [formErrors, setFormErrors] = useState({});

  const bloodTypes = ["A+", "A-", "B+", "B-", "AB+", "AB-", "O+", "O-"];

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email) {
      setEmailError("Email is required");
      return false;
    } else if (!emailRegex.test(email)) {
      setEmailError("Please enter a valid email address");
      return false;
    } else {
      setEmailError("");
      return true;
    }
  };

  const validatePassword = (password) => {
    const errors = {
      length: password.length >= 8 && password.length <= 12,
      number: /\d/.test(password),
      uppercase: /[A-Z]/.test(password),
      lowercase: /[a-z]/.test(password),
      special: /[!@#$%^&*(),.?":{}|<>]/.test(password),
    };
    setPasswordErrors(errors);
    return Object.values(errors).every(Boolean);
  };

  const validateForm = () => {
    const errors = {};
    const today = new Date();
    const birthDate = new Date(formData.dateOfBirth);
    const age = today.getFullYear() - birthDate.getFullYear();

    if (!formData.firstName) errors.firstName = "First name is required";
    if (!formData.lastName) errors.lastName = "Last name is required";
    if (!formData.dateOfBirth) {
      errors.dateOfBirth = "Date of birth is required";
    } else if (age < 18) {
      errors.dateOfBirth = "You must be at least 18 years old to register";
    }
    if (!formData.phone) {
      errors.phone = "Phone number is required";
    } else if (!/^\d{10}$/.test(formData.phone.replace(/[-()\s]/g, ""))) {
      errors.phone = "Please enter a valid 10-digit phone number";
    }
    if (!formData.bloodType) errors.bloodType = "Blood type is required";
    if (!formData.weight) {
      errors.weight = "Weight is required";
    } else if (Number(formData.weight) < 50) {
      errors.weight = "Minimum weight requirement is 50kg";
    }
    if (!formData.address) errors.address = "Address is required";

    setFormErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const isEmailValid = validateEmail(formData.email);
    const isPasswordValid = validatePassword(formData.password);
    const isFormValid = validateForm();
    const doPasswordsMatch = formData.password === formData.confirmPassword;
    setPasswordMatch(doPasswordsMatch);

    if (isEmailValid && isPasswordValid && isFormValid && doPasswordsMatch) {
      console.log("Registration attempted with:", formData);
    }
  };

  const handleInputChange = (id, value) => {
    setFormData((prev) => ({
      ...prev,
      [id]: value,
    }));
  };

  const PasswordCriteria = ({ met, text }) => (
    <div className="flex items-center gap-2">
      <div
        className={`w-2 h-2 rounded-full ${
          met ? "bg-green-500" : "bg-red-500"
        }`}
      />
      <span
        className={`text-sm ${isDarkMode ? "text-gray-300" : "text-gray-600"}`}
      >
        {text}
      </span>
    </div>
  );

  const InputField = ({ id, label, type = "text", icon: Icon, ...props }) => (
    <div>
      <label
        htmlFor={id}
        className={`block text-sm font-medium ${
          isDarkMode ? "text-gray-300" : "text-gray-700"
        }`}
      >
        {label}
      </label>
      <div
        className={`mt-1 relative rounded-lg shadow-sm overflow-hidden ${
          focusedInput === id ? "ring-2 ring-red-500 ring-opacity-50" : ""
        } ${formErrors[id] ? "ring-2 ring-red-500" : ""}`}
      >
        {Icon && (
          <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
            <Icon
              className={`h-5 w-5 ${
                focusedInput === id ? "text-red-500" : "text-gray-400"
              }`}
            />
          </div>
        )}
        <input
          id={id}
          name={id}
          type={type}
          className={`block w-full ${Icon ? "pl-10" : "pl-3"} pr-3 py-3 ${
            isDarkMode ? "bg-gray-700 text-white" : "bg-white text-gray-900"
          } rounded-lg focus:outline-none transition-all duration-200`}
          onFocus={() => setFocusedInput(id)}
          onBlur={() => {
            // Only clear focus if we're not clicking within the same input
            setTimeout(() => {
              if (focusedInput === id) {
                setFocusedInput("");
              }
            }, 100);
          }}
          value={formData[id]}
          onChange={(e) => handleInputChange(id, e.target.value)}
          {...props}
        />
      </div>
      {formErrors[id] && (
        <p className="mt-1 text-sm text-red-500">{formErrors[id]}</p>
      )}
    </div>
  );

  return (
    <div
      className={`min-h-screen w-full ${
        isDarkMode
          ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900"
          : "bg-gradient-to-br from-red-50 via-white to-red-50"
      } py-12 px-4 sm:px-6 lg:px-8`}
    >
      <button
        onClick={() => setIsDarkMode(!isDarkMode)}
        className="fixed top-4 right-4 p-2 rounded-full bg-white/80 backdrop-blur-sm shadow-lg hover:bg-gray-100 transition-colors duration-200"
      >
        {isDarkMode ? (
          <Sun className="h-6 w-6 text-yellow-500" />
        ) : (
          <Moon className="h-6 w-6 text-gray-800" />
        )}
      </button>

      <div className="max-w-2xl mx-auto">
        <div className="text-center">
          <Heart
            className={`mx-auto h-16 w-16 ${
              isDarkMode ? "text-red-600" : "text-red-500"
            }`}
          />
          <h2
            className={`mt-6 text-4xl font-bold ${
              isDarkMode ? "text-white" : "text-gray-900"
            }`}
          >
            Register as a Donor
          </h2>
          <p
            className={`mt-2 text-lg ${
              isDarkMode ? "text-gray-300" : "text-gray-600"
            }`}
          >
            Join our community of life-savers
          </p>
        </div>

        <div
          className={`mt-8 ${
            isDarkMode ? "bg-gray-800/80" : "bg-white/80"
          } backdrop-blur-sm py-8 px-4 shadow-2xl sm:rounded-2xl sm:px-10`}
        >
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
              <InputField
                id="firstName"
                label="First Name"
                icon={User}
                placeholder="Enter your first name"
              />
              <InputField
                id="lastName"
                label="Last Name"
                icon={User}
                placeholder="Enter your last name"
              />
            </div>

            <InputField
              id="email"
              label="Email address"
              type="email"
              icon={Mail}
              placeholder="Enter your email"
            />
            {emailError && (
              <p className="mt-1 text-sm text-red-500">{emailError}</p>
            )}

            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
              <div>
                <InputField
                  id="password"
                  label="Password"
                  type={showPassword ? "text" : "password"}
                  icon={Lock}
                  placeholder="Enter your password"
                />
                {(focusedInput === "password" || formData.password) && (
                  <div className="mt-2 space-y-2">
                    <PasswordCriteria
                      met={passwordErrors.length}
                      text="8-12 characters long"
                    />
                    <PasswordCriteria
                      met={passwordErrors.number}
                      text="Contains a number"
                    />
                    <PasswordCriteria
                      met={passwordErrors.uppercase}
                      text="Contains an uppercase letter"
                    />
                    <PasswordCriteria
                      met={passwordErrors.lowercase}
                      text="Contains a lowercase letter"
                    />
                    <PasswordCriteria
                      met={passwordErrors.special}
                      text="Contains a special character"
                    />
                  </div>
                )}
              </div>
              <InputField
                id="confirmPassword"
                label="Confirm Password"
                type={showPassword ? "text" : "password"}
                icon={Lock}
                placeholder="Confirm your password"
              />
            </div>
            {!passwordMatch && (
              <p className="text-sm text-red-500">Passwords do not match</p>
            )}

            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
              <InputField
                id="dateOfBirth"
                label="Date of Birth"
                type="date"
                icon={Calendar}
              />
              <InputField
                id="phone"
                label="Phone Number"
                icon={Phone}
                placeholder="Enter your phone number"
              />
            </div>

            <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Blood Type
                </label>
                <div className="mt-1">
                  <select
                    value={formData.bloodType}
                    onChange={(e) =>
                      handleInputChange("bloodType", e.target.value)
                    }
                    className={`block w-full pl-3 pr-10 py-3 ${
                      isDarkMode
                        ? "bg-gray-700 text-white"
                        : "bg-white text-gray-900"
                    } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500`}
                  >
                    <option value="">Select blood type</option>
                    {bloodTypes.map((type) => (
                      <option key={type} value={type}>
                        {type}
                      </option>
                    ))}
                  </select>
                </div>
                {formErrors.bloodType && (
                  <p className="mt-1 text-sm text-red-500">
                    {formErrors.bloodType}
                  </p>
                )}
              </div>
              <InputField
                id="weight"
                label="Weight (kg)"
                type="number"
                placeholder="Enter your weight"
                min="0"
              />
            </div>

            <InputField
              id="lastDonation"
              label="Last Donation Date (if any)"
              type="date"
              icon={Droplets}
            />

            <div>
              <label
                className={`block text-sm font-medium ${
                  isDarkMode ? "text-gray-300" : "text-gray-700"
                }`}
              >
                Medical Conditions (if any)
              </label>
              <textarea
                value={formData.medicalConditions}
                onChange={(e) =>
                  handleInputChange("medicalConditions", e.target.value)
                }
                className={`mt-1 block w-full px-3 py-2 ${
                  isDarkMode
                    ? "bg-gray-700 text-white"
                    : "bg-white text-gray-900"
                } rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500`}
                rows="3"
                placeholder="List any medical conditions or medications"
              />
            </div>

            <InputField
              id="address"
              label="Address"
              placeholder="Enter your full address"
            />

            <button
              type="submit"
              className={`w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white ${
                isDarkMode
                  ? "bg-gradient-to-r from-red-700 to-red-600"
                  : "bg-gradient-to-r from-red-600 to-red-500"
              } hover:from-red-700 hover:to-red-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 transition-all duration-200`}
            >
              Register as Donor
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default DonerRegistration;
